'use client';

import { useRouter } from 'next/navigation';
import StopsList from '@/app/components/stoplist';

const RoutePage = ({ params }: { params: { routeid: string } }) => {
    if (!params.routeid) return <p>Loading...</p>;

    return (
        <div>
            <h1>Route {params.routeid}</h1>
            <StopsList routeId={params.routeid} />
        </div>
    );
};

export default RoutePage;
